html {
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pagination {
  display: flex;
  border-radius: 0.25rem;
  padding-left: 0;
  list-style: none;

  &__info {
    display: flex;
    justify-content: center;
    line-height: 2rem;
    width: 40px;
    height: 20px;

  }

  &__page {
    outline: none;
    cursor: pointer;
    padding: 0.5rem 0.75rem;
    line-height: 1.25;
    color: #007bff;
    background-color: #fff;
    border: 1px solid #dee2e6;

    &--disabled {
      color: #6c757d;
      pointer-events: none;
      cursor: auto;
      background-color: #fff;
      border-color: #dee2e6;
    }

    &--active {
      color: #fff;
      background-color: #007bff;
      border-color: #007bff;
    }
  }
}

.users {
  margin: 0 auto;
  border-collapse: collapse;
  width: 75%;
}

.users td,
.users th {
  border: 1px solid #ddd;
  padding: 8px;
}

.users tr:nth-child(even) {
  background-color: #f2f2f2;
}

.users td:nth-child(2) {
  text-align: left;
}

.users tr:hover {
  background-color: #ddd;
}

.users th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #e09b14;
  color: white;
}
